import React from 'react';

import { StyledImageRotate, StyledRotate } from './styles';

export const Rotate = () => (
  <StyledRotate>
    <StyledImageRotate />
  </StyledRotate>
);

export default Rotate;
Rotate.displayName = 'Rotate';



